@import './sass/variables';
@import './sass/mixin';

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: $fontFamily_1, $fontFamily_2,
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: $bgcSecond;
}

