.container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.container img {
    width: 50px;
    height: 50px;
    border-radius: 30px;
    margin-top: 5px;
    margin-left: 25px;
    box-shadow: 3px 3px 10px 0 rgba(0, 0, 0, 0.5);
}

.container_2 {
    width: 500px;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid lightgrey;
    margin-top: 10px;
    padding-bottom: 10px;
}

.imgBtn {

}

.information {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.presentContainer {
    display: flex;
    justify-content: center;
    align-items: center;
}

.loader {
    background-image: url("https://icons8.com/preloaders/preloaders/828/3D%20Yin-yang.gif");
    background-size: cover;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    margin-top: 20vh;
    margin-left: 20vw;
    margin-bottom: 20vh;
}

